import { useState, useEffect } from "react";
import axios from "axios";
import Header from "../components/Header";
import Link from "next/link";

const Clients = (props: any) => {
  const [clients, setClient] = useState([]);

  useEffect(() => {
    setClient(props.data);
  });

  const ClientsList = (props : any) => {
    // return props.clients.map(e => {
    //   <li key={e._id}>
    //     <Link href="/clientici/[id]" as={`/clientici/${e._id}`}>
    //       <a>{e.name}, {e.age}, {e.address}</a>
    //     </Link>
    //   </li>;
    // });
    return props.clients.map(e => <li>{e.name}</li>);
  };

  if (clients.length === 0) {
    return "Loading";
  }
  return (
    <div>
      <Header />
      <h1>List of all clients</h1>
      <ul>
        <ClientsList clients={clients} />
      </ul>
    </div>
  );
};

Clients.getInitialProps = async () => {
  const res = await axios({
    method: "get",
    url: "http://localhost:8080/api/clients",
    responseType: "json"
  });
  const data = await res.data;
  return {
    data
  };
};

export default Clients;
